$white:#ffffff;
$darkgrey: #444444;
$lightgrey: #f2f2f2;
$green: #68b738;

$tablet-width: 768px;
$desktop-width: 1440px;
$mobile-width-only: 767px;

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}
